/** @jsx jsx */
import React from 'react'
import { Link } from 'gatsby'
import { jsx, Styled } from 'theme-ui'
import * as components from '@theme-ui/components'
import { Global } from '@emotion/core'

import * as blocks from '@blocks/react'
import { InlineRender } from 'blocks-ui'
import * as controls from 'property-controls'

import SEO from './seo'

const { Container } = components

const isComponent = name => /^[A-Z]/.test(name)

const scope = {
  React,
  jsx,
  Styled,
  ...components,
  ...blocks,
  ...controls
}

const PropertyControlsTable = ({ controls, name }) => (
  <Styled.table sx={{ m: 0 }}>
    <Styled.tr>
      <Styled.th sx={{ width: '33%' }}>
        <Styled.code>{name}</Styled.code>
      </Styled.th>
      <Styled.th sx={{ width: '16%' }}>Type</Styled.th>
      <Styled.th sx={{ width: '33%' }}>Default Value</Styled.th>
    </Styled.tr>
    {Object.entries(controls).map(([key, val]) => (
      <Styled.tr key={key}>
        <Styled.td>{val.title || key}</Styled.td>
        <Styled.td>
          <Styled.code>{val.type}</Styled.code>
        </Styled.td>
        <Styled.td>{val.defaultValue || 'None'}</Styled.td>
      </Styled.tr>
    ))}
  </Styled.table>
)

const Block = ({ block }) => {
  /*eslint import/namespace: [2, { allowComputed: true }]*/
  const component = blocks[block.displayName]

  const components = Object.entries(component).reduce((acc, [key, val]) => {
    if (isComponent(key)) {
      const componentName = [block.displayName, key].join('.')
      acc[componentName] = val
    }

    return acc
  }, {})

  return (
    <Styled.root>
      <Global
        styles={{
          '*': {
            boxSizing: 'border-box'
          },
          body: {
            margin: 0
          }
        }}
      />
      <SEO title={block.displayName} />
      <header
        sx={{
          display: 'flex',
          width: '100%',
          alignItems: 'center',
          py: 2,
          px: 3,
          borderBottom: '1px solid',
          borderColor: 'border'
        }}
      >
        <Link to="/blocks">
        <svg width="25" height="25" viewBox="0 0 250 250" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M125 134.757L61 107.859V194.402L125 221.3V134.757Z" fill="black" />
          <path d="M125 134.757L189 107.859V194.402L125 221.3V134.757Z" fill="black" />
          <path d="M61 107.859L125 83.2998L189 107.859L125 134.757L61 107.859Z" fill="black" />
          <path d="M122 139.3L67 116.3V190.3L122 213.3V139.3Z" fill="white" />
          <path d="M128 139.3L183 116.3V190.3L128 213.3V139.3Z" fill="white" />
          <path d="M70 111.3L125 90.2998L180 111.3L125 134.3L70 111.3Z" fill="white" />
          <path d="M186 160.398L122 133.499V220.042L186 246.94V160.398Z" fill="black" />
          <path d="M186 160.398L250 133.499V220.042L186 246.94V160.398Z" fill="black" />
          <path d="M122 133.499L186 108.94L250 133.499L186 160.398L122 133.499Z" fill="black" />
          <path d="M183 164.94L128 141.94V215.94L183 238.94V164.94Z" fill="white" />
          <path d="M189 164.94L244 141.94V215.94L189 238.94V164.94Z" fill="white" />
          <path d="M131 136.94L186 115.94L241 136.94L186 159.94L131 136.94Z" fill="white" />
          <path d="M64 160.398L0 133.499V220.042L64 246.94V160.398Z" fill="black" />
          <path d="M64 160.398L128 133.499V220.042L64 246.94V160.398Z" fill="black" />
          <path d="M0 133.499L64 108.94L128 133.499L64 160.398L0 133.499Z" fill="black" />
          <path d="M61 164.94L6 141.94V215.94L61 238.94V164.94Z" fill="white" />
          <path d="M67 164.94L122 141.94V215.94L67 238.94V164.94Z" fill="white" />
          <path d="M9 136.94L64 115.94L119 136.94L64 159.94L9 136.94Z" fill="white" />
          <path d="M125 54.4576L61 27.5593V114.102L125 141V54.4576Z" fill="black" />
          <path d="M125 54.4576L189 27.5593V114.102L125 141V54.4576Z" fill="black" />
          <path d="M61 27.5593L125 3L189 27.5593L125 54.4576L61 27.5593Z" fill="black" />
          <path d="M122 59L67 36V110L122 133V59Z" fill="white" />
          <path d="M128 59L183 36V110L128 133V59Z" fill="white" />
          <path d="M70 31L125 10L180 31L125 54L70 31Z" fill="white" />
        </svg>
          {/* <img
            alt="logo"
            src="src/assets/images/favicon.png"
            sx={{
              mt: -1,
              height: 24,
              verticalAlign: 'middle',
              mr: 2
            }}
          /> */}
        </Link>
        <Link
          to="/blocks"
          sx={{
            color: 'inherit',
            textDecoration: 'none',
            fontSize: [1, 1, 1],
            lineHeight: 1
          }}
        >
          WebBuilder
        </Link>
        <span sx={{ px: 1, fontSize: 0 }}>/</span>
        <Styled.h1
          sx={{
            m: 0,
            lineHeight: 1,
            fontWeight: 'body',
            fontSize: [1, 1, 1]
          }}
        >
          {block.displayName}
        </Styled.h1>
      </header>
      <main>
        <section
          sx={{
            mb: [3, 4, 5],
            p: [3, 4, 5],
            borderBottom: '1px solid',
            borderColor: 'border',
            backgroundColor: '#fafafa'
          }}
        >
          <InlineRender
            sx={{
              backgroundColor: 'background'
            }}
            scope={scope}
            code={block.transformed}
          />
        </section>
        <Container>
          <Styled.h3>Property controls</Styled.h3>
          <PropertyControlsTable
            name={block.displayName}
            controls={component.propertyControls}
          />
          {Object.entries(components)
            .filter(([_, val]) => !!val.propertyControls)
            .map(([key, val]) => (
              <PropertyControlsTable
                key={key}
                name={key}
                controls={val.propertyControls}
              />
            ))}
          <Styled.h3 sx={{ mt: [3, 4, 5] }}>Example usage</Styled.h3>
          <Styled.pre>
            {"/** @jsx jsx */\nimport { jsx } from 'theme-ui'\n" +
              `import { ${block.displayName} }  from '@blocks/components'` +
              '\n\n' +
              'export default () => (\n  ' +
              component.usage.trim() +
              '\n)'}
          </Styled.pre>
          <Styled.h3>Source code</Styled.h3>
          <Styled.pre>{block.src}</Styled.pre>
        </Container>
      </main>
    </Styled.root>
  )
}

export default Block
